import React, {useState} from 'react';
import './App.css';
import {answerAPI} from "./api/api";
import {Button, Container, Stack, Tooltip} from "@mui/material";
import ThumbsUpDownIcon from '@mui/icons-material/ThumbsUpDown';


function App() {

    const [image, setImage] = useState('')


    const getAnswer = async () => {
        const answer = await answerAPI.ask()
        setImage(answer.data.image)
    }

    const longText = `Нажми для получения ответа с сервера`


    return (
        <Container>
            <Stack direction="column"
                   justifyContent="center"
                   alignItems="center"
                   spacing={5}>
                <h1><ThumbsUpDownIcon/> Нужен знак свышe?</h1>
                <Tooltip title='Нажми для получения ответа с сервера'>
                    <Button onClick={getAnswer} variant="contained">Ну жми</Button>
                </Tooltip>
                <div>
                    <img src={image} alt=""/>
                </div>
            </Stack>
        </Container>

    );
}

export default App;
